}
@font-face {
  font-family: "TTFirsNeue";
  src: url("../fonts/../fonts/TTFirsNeue-Bold.woff") format("woff");
  src: url("../fonts/../fonts/TTFirsNeue-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "TTFirsNeue", sans-serif;
  font-weight: 400;
  background-color: var(--color-white);
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

a:focus {
  border: none;
  outline: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}
@media (max-width: 767px) {
  .for-mobile {
    display: block;
  }
}

.for-tablet {
  display: none;
}
@media (max-width: 1023px) {
  .for-tablet {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.btn-reset {
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
}
.btn-reset:focus {
  border: none;
  outline: none;
}

.input-reset {
  border: none;
  background-color: transparent;
}
.input-reset:focus {
  border: none;
  outline: none;
}

.textarea-reset {
  resize: none;
}

.container {
  margin: 0 auto;
  padding-left: var(--offset);
  padding-right: var(--offset);
  max-width: var(--container-width);
}
@media (max-width: 1199px) {
  .container {
    padding-left: var(--offset-tablet-album);
    padding-right: var(--offset-tablet-album);
    max-width: var(--container-width-tablet-album);
  }
}
@media (max-width: 1023px) {
  .container {
    padding-left: var(--offset-tablet-portrait);
    padding-right: var(--offset-tablet-portrait);
    max-width: var(--container-width-tablet-portrait);
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: var(--offset-mobile);
    padding-right: var(--offset-mobile);
    max-width: var(--container-width-mobile);
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.burger {
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  height: var(--burger-line-height);
  background-color: var(--color-white);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
  width: 100%;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
  width: 50%;
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: var(--color-white);
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}